<template>
  <div class="all">
    <Header/>

    <div class="item">
      <h1 v-html="noteDetail.title">{{}}</h1>
      <h2> 作者 {{noteDetail.username}}</h2>
      <div class="detail">
        <p v-html="noteDetail.note"></p>
      </div>
    </div>
    <div id="reply-all">
      <h3>回复:</h3>
      <div v-for="item in replyData" :key="item.index" id="reply">
        <p>{{item.username}} :</p>
        <p type>{{item.replyContent}}</p>
      </div>
    </div>


    <div class="reply">
      <button @click="handleReply">回复</button>
      <input type="text" v-model="replyContent">
    </div>
  </div>
</template>

<script>
  import Header from '../components/Header'
  import axios from 'axios'

  export default {
    name: "detail",
    components: {
      Header
    },
    data() {
      return {
        noteList: [],
        noteDetail: "",
        userStatus: {},
        replyData: [],
        replyContent: "",
        estimate: ""
      }
    },
    methods: {
      getDetail() {
        let _id = this.$route.query.id;
        this.$axios.post('/note/detail', {_id}).then(res => {
          this.noteDetail = res.data.data;
          this.replyData = res.data.rda;
          // console.log(res.data);
        })
      },
      handleReply() {
        if (this.estimate == 200) {
          if (!this.replyContent == "") {
            let replyContent = this.replyContent;
            let userId = this.userStatus.userId;
            let noteId = this.$route.query.id;
            this.$axios.post('/note/reply', {replyContent, userId, noteId}).then(res => {
              // console.log(res.data);
              this.replyContent = "";
              this.getDetail();
            })
          } else {
            alert("回复内容不能为空")
          }
        } else {
          alert("请先登录")
        }
      },
      getUser() {
        this.$axios.get('/getMsg').then(res => {
          this.estimate = res.data.code
          this.userStatus = res.data.data
        })
      }
    },
    created() {
      this.getDetail();
      this.getUser();
    }
  }
</script>

<style scoped lang="less">
  #reply-all{
    width: 780px;
    margin: 20px auto 20px auto;
    background: skyblue;
    padding: 20px 10px;
    h3{
      margin-bottom: 14px;
    }
    #reply {
      border: 1px solid #ccc;
      margin: 20px auto 20px auto;
      padding: 20px 10px;
      p[type]{
        margin: 6px;
        text-indent: 2em;
      }
    }
  }

  .item {
    padding-top: 14px;
    text-align: center;
    width: 800px;
    margin: 50px auto 0 auto;
    background: #eee;
    h1 {
      letter-spacing: 6px;
      font-weight: 400;
      font-size: 38px;
      margin: 16px 0;
    }
    h2 {
      font-size: 14px;
      font-weight: 200;
      margin: 10px 0;
    }
    .detail {
      padding: 0 14px;
      text-align: left;
      margin-top: 30px;
      text-indent: 2rem;
      letter-spacing: 2px;
      font-weight: 100;
      line-height: 32px;
      font-size: 18px;
    }
  }

  .reply {
    width: 800px;
    margin: 40px auto 100px auto;
    button {
      width: 60px;
      height: 34px;
    }
    input {
      width: 400px;
      height: 30px;
    }
  }

</style>



